@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Ubuntu:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #580880;
    --color-primary: #b336f6;
    --color-primary-variant: #0b826b;
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    font-family: 'Ubuntu', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/assets/bgt1.png); */
}

/* ========================GENERAL STYLES */
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    height: 275vh;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-primary);
    font-size: 40px;
}

/* .cta {
    color: var(--color-primary-variant);
} */

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: 40%;
    height: 110%;
    display: inline-block;
    color: var(--color-bg);
    background-color: var(--color-white);
    padding: 0.75 rem 1.2 rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary-variant);
    transition: var(transition);
    text-align: center;
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary-variant);
    color: var(--color-white);
    width:  40%;
    height: 110%;
}

.btn-github {
        width: 100%;
        height: 75%;
        display: inline-block;
        color: var(--color-white);
        background-color: var(--color-primary-variant);
        padding: 0.75 rem 1.2 rem;
        border-radius: 0.4rem;
        cursor: pointer;
        border: 1px solid var(--color-primary-variant);
        transition: var(transition);
        text-align: center;
        margin-top: 2rem;
        margin-bottom: 0.1rem;
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERIES (TABLETS)*/

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* MEDIA QUERIES (PHONES)*/

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}